.jenkins-search::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 0;
  height: 0;
  color: inherit;
  border: 2px solid currentColor;
  border-radius: 100%;
  clip-path: inset(0 0 50% 50%);
  opacity: 0;
  transition: var(--standard-transition);
  animation: loading-spinner 1s infinite linear;
